.pagination {
  @include list-reset;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 50px 0 0 0;
  color: $grey;

  @media (max-width: $tablet-width) {
    padding: 40px 0 0 0;
  }

  @media (max-width: $mobile-width) {
    padding: 30px 0 0 0;
  }
}

.pagination--catalog {
  padding: 0;
}

.pagination__item {
  margin-right: 12px;
  cursor: pointer;

  &:nth-child(2n) {
    @media (max-width: $mobile-width) {
      margin-left: 12px;
    }
  }

  &:last-child {
    margin-right: 0;
  }
}

.pagination__item--active {
  color: $liquorice;
}
